$current_block: null;
$current_elements: null;
$bem_level: root;

// TODO: look into incorperating some of the ideas presented here:
// https://medium.com/@marcmintel/pushing-bem-to-the-next-level-with-sass-3-4-5239d2371321

@mixin _bem-level($level) {
  $old_bem_level: $bem_level;
  $bem_level: $level !global;
  @content;
  $bem_level: $old_bem_level !global;
}

// The top level name of a component. If you are referring to block within another block it will be nested under the
// other block.
// output:
//
// .block
// .block .block

@mixin block($b) {
  $old_block: $current_block;
  $current_block: '.#{$b}' !global;

  @if ($old_block == null) {

    @include _bem-level(block) {
      @at-root #{$current_block} { @content }
    }

  } @else {

    @include _bem-level(block) {
      #{$current_block} { @content }
    }

  }

  $current_block: $old_block !global;
}

// All the elements that compose a block. They should be included directly below a block.
// This includes elements that are nested within each other.
// output:
//
// .block__element
// .block--modifier block__element

// TODO: a user could technically use this under an element modifier right now, not sure if that's undesirable or not.

@mixin element($elements...) {

  $selectors: ();

  @if ($bem_level == block) {

    @each $s in & {
      @each $e in $elements {
        $selectors: append($selectors, '#{$s}__#{$e}', comma);
      }
    }

  } @else if ($bem_level == modifier) {

    @each $s in & {
      @each $e in $elements {
        $selectors: append($selectors, '#{$s} #{$current_block}__#{$e}', comma);
      }
    }

  } @else {
    @error 'Expected element to be declared inside of a block or block modifier but instead was declared under #{$bem_level}';
  }

  @include _bem-level(element) {
    @at-root #{$selectors} {
      @content;
    }
  }
}

// Any "change to" or "variation of" a block or element
// output:
//
// .block--modifier
// .block__element--modifier

@mixin modifier($modifiers...) {
  @if not ($bem_level == block or $bem_level == element) {
    @error 'Expected modifier to be declared inside of a block or element, but instead was declared inside a #{$bem_level}'
  }

  $selectors: ();
  @each $s in & {
    @each $m in $modifiers {
      $selectors: append($selectors, '#{$s}--#{$m}', comma);
    }
  }

  @include _bem-level(modifier) {
    @at-root #{$selectors} {
      @content
    }
  }
}


// makes changes to a block or element when a specific combination of modifiers are applied
// output:
//
// .block--modifier1.block--modifier2


// TODO: add the ability to mix mutiple modifiers at a time e.g. @include modifier-mix((red, blue, green), large) {...}

@mixin modifier-mix($modifiers...) {
  @if not ($bem_level == block or $bem_level == element or $bem_level == modifer) {
    @error 'Expected modifier to be declared inside of a block or element, but instead was declared inside a #{$bem_level}'
  }

  $selectors: ();

  @each $s in & {
    $mix: '';
    @each $m in $modifiers {
      $mix: $mix + '#{$s}--#{$m}'
    }
    $selectors: append($selectors, $mix, comma);
  }

  @include _bem-level(modifier) {
    @at-root #{$selectors} {
      @content
    }
  }


}

// Makes changes to an element based on whether a given modifier is included on a block
// output:
//
// .block--modifier .block__element
//
// Note: this is the only method that relies on specificity. Although the specificity override is generally
// probably what you're looking for, this could be annoying with multiple modifiers.

@mixin block-modifier($modifiers...) {
  @if ($bem_level != element) {
    @error 'Must be called in the context of an element'
  }

  $selectors: ();

  @each $s in & {
    @each $m in $modifiers {
      $selectors: append($selectors, '#{$current_block}--#{$m} #{$s}', comma);
    }
  }

  @include _bem-level(modifier) {
    @at-root #{$selectors} {
      @content;
    }
  }
}
